.navigation {
  font-size: 1.2rem;
  position: absolute;
  left: 20px;
  top: 20px;
  &::before {
    content: "";
    position: absolute;
    transform: translate(-50%, -50%);
    height: 450px;
    width: 450px;
    border-radius: 50%;
    opacity: 0.1;
    background: $color1;
    z-index: -2;
  }
  &:hover {
    border-radius: 50%;
    &::before {
      opacity: 0.4;
      height: 500px;
      width: 500px;
      transition: 0.2s linear;
    }
  }
  ul {
    line-height: 2rem;
    li {
      color: $color2;
      cursor: pointer;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        transform: scaleX(0);
        width: 100%;
        height: 2px;
        bottom: 0;
        left: 0;
        background: $color4;
        transform-origin: bottom right;
        transition: transform 0.25s ease;
      }
      &:hover {
        font-weight: 250;
        animation: anim 1.5s forwards;
        color: $color4 !important;
        &::after {
          transform: scaleX(1);
          transform-origin: bottom left;
        }
      }
    }
    a {
      text-decoration: none;
    }
  }
}

.nav-active {
  li {
    color: $color3 !important;
  }
}
.nav-portfolio {
  display: flex;
  position: relative;
  &:hover {
    .nav-projects {
      transition: 0.5s ease-out;
      opacity: 0.8;
    }
  }
  .nav-projects {
    opacity: 0;
    position: absolute;
    left: 100px;
    width: 110%;
  }
}
