.notFound {
  @include verticalCenter;
  height: 100vh;
  background: $color1;
}
.notFound-content {
  border-radius: 20px;
  text-align: center;
  h2 {
    font-size: 2rem;
    letter-spacing: 2px;
    padding-bottom: 20px;
  }
  a {
    color: $bgcolor;
    text-decoration: none;
    :hover {
      transition: 0.4s ease;
      text-decoration: underline;
      color: $color3;
    }
  }
}
