.contact-container {
  height: 100%;
  width: 100%;
  @include verticalCenter;
  .form-container {
    display: grid;
    justify-items: center;
    background-color: $color4;
    padding: 20px;
    border-radius: 20px;

    input,
    textarea {
      resize: none;
      padding: 20px;
      width: 450px;
      margin: 20px;
      border: 2px solid $color2;
      border-radius: 20px;
      &:focus {
        outline: none;
      }
    }
    label {
      letter-spacing: 2px;
      color: $color1;
    }
    input[type="submit"] {
      background: $bgcolor;
      color: $color1;
      border: 1px solid $color1;
      border-radius: 50px;
      letter-spacing: 3px;
      width: 90%;
      display: inline-block;
      &:hover {
        animation: anim 0.8s ease;
        color: $bgcolor;
        background: $color1;
        cursor: pointer;
      }
    }
  }
  #message-display {
    color: $color2;
  }
}
