@font-face {
  font-family: "font-2";
  src: url(../assets/fonts/OpenSans-Regular.ttf);
}
@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/RobotoFlex.ttf);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
$bgcolor: #e0e1dd;
$color1: #778da9;
$color2: #415a77;
$color3: #1b263b;
$color4: #0d1b2a;

$font-1: "font-1", sans-serif;
$font-2: "font-2", sans-serif;

body {
  background: $bgcolor;
  font-family: $font-1;
  color: $color4;
  overflow: hidden;
}

main {
  height: 100vh;
}

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

li {
  list-style-type: none;
}
