.dynamic-text {
  @include verticalCenter;
  height: 100%;
  font-size: 3rem;
  &::after {
    content: "";
    position: absolute;
    bottom: -400px;
    height: 600px;
    width: 600px;
    border-radius: 50%;
    opacity: 0.1;
    background: $color1;
    z-index: -2;
    animation: circleAnim 10s infinite alternate; /* Appliquer l'animation*/
  }
}

@keyframes circleAnim {
  0% {
    width: 500px; /*Taille initiale*/
    height: 500px;
  }
  50% {
    width: 600px; /*Taille maximale*/
    height: 600px;
  }
  100% {
    width: 500px; /* Taille initiale*/
    height: 500px;
  }
}

.text-target {
  margin-left: 10px;
}

@keyframes anim {
  0% {
    font-weight: 250;
  }
  50% {
    font-weight: 900;
  }
  100% {
    font-weight: 250;
  }
}
