.scroll-bottom {
  a {
    color: $color3;
    font-size: 2.5rem;
    transform: scale(1);
    &:hover {
      transform: scale(2);
      color: $color2;
      svg {
        transition: unset;
        animation: unset;
      }
    }
    svg {
      transition: transform 1s ease-in-out;
      animation: bounceX 3s infinite;
    }
  }
}

.right {
  position: absolute;
  top: 50%;
  right: 5%;
}
.left {
  position: absolute;
  top: 50%;
  left: 5%;
}

@keyframes bounceX {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(-10px);
  }
  60% {
    transform: translateX(-20px);
  }
}
