.social-network {
  position: absolute;
  right: 20px;
  top: 20px;
}

.content {
  @include verticalCenter;
  a {
    transition: transform 0.25s ease;
  }
  li {
    padding: 20px;
    font-size: 1.2rem;
    color: $color1;
    transition: font-size 0.25s ease;

    &:hover {
      color: $color3;
      font-size: 1.4rem;
    }
  }
}
