.project-container {
  @include verticalCenter;
  height: 100%;
  .navigation {
    left: 50%;
  }
}

.project {
  .projectDisplay {
    @include verticalCenter;
    flex-wrap: wrap;

    .left-container {
      p {
        padding: 20px auto;
        font-size: 1.1rem;
        font-style: italic;
      }
      li {
        font-weight: 500;
        padding: 10px;
        font-size: 1.3rem;
      }
    }
    .right-container {
      position: relative;
      width: 50%;
      padding-left: 30px;
      @include verticalCenter;
      &:hover {
        .info-container {
          opacity: 1;
        }
        img {
          opacity: 0.2;
        }
      }
      .info-container {
        text-align: center;
        opacity: 0;
        transition: opacity 500ms linear;
        z-index: 99;
        padding: 20px;
        h3 {
          padding-bottom: 20px;
        }
      }

      img {
        transform: translate(-50%, -50%);
        position: absolute;
        top: 30%;
        left: 50%;
        border-radius: 10%;
        transition: opacity 500ms linear;
      }
      .btn-container {
        text-align: center;
        .btn {
          margin-top: 50px;
          text-decoration: none;
          background: $bgcolor;
          color: $color1;
          border: 1px solid $color1;
          padding: 30px;
          border-radius: 50px;
          letter-spacing: 3px;
          width: 200px;
          max-width: 200px;
          display: inline-block;
          &:hover {
            animation: anim 2s infinite;
            color: $bgcolor;
            background: $color1;
          }
        }
      }
    }
  }
}
